'use client';

import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import { QuizFilters } from "@/components/dashboard/student/filters/QuizFilters";
import { QuizCard } from "@/components/dashboard/student/quiz-card";
import { QuizType } from "@/app/dashboard/student/types/types";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Skeleton } from "@/components/ui/skeleton";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { 
  BookOpen, 
  AlertCircle,
  RefreshCw
} from "lucide-react";
import { Pagination, PaginationContent, PaginationEllipsis, PaginationItem, PaginationLink } from "@/components/ui/pagination";
import { useToast } from "@/components/ui/use-toast";

// Loading skeleton for quiz card
function QuizCardSkeleton() {
  return (
    <Card className="overflow-hidden">
      <div className="p-6">
        <div className="space-y-5">
          <div className="flex gap-2">
            <Skeleton className="h-5 w-24" />
            <Skeleton className="h-5 w-20" />
          </div>
          <Skeleton className="h-6 w-4/5" />
          <Skeleton className="h-4 w-2/3" />
          <div className="space-y-2">
            <Skeleton className="h-4 w-full" />
            <Skeleton className="h-4 w-5/6" />
            <Skeleton className="h-4 w-3/4" />
          </div>
          <Skeleton className="h-9 w-full" />
        </div>
      </div>
    </Card>
  );
}

export function QuizzesPageContent() {
  const { toast } = useToast();
  const searchParams = useSearchParams();
  
  // State
  const [activeTab, setActiveTab] = useState<string>("all");
  const [quizzes, setQuizzes] = useState<QuizType[]>([]);
  const [filteredQuizzes, setFilteredQuizzes] = useState<QuizType[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [totalItems, setTotalItems] = useState(0);
  const [isRefreshing, setIsRefreshing] = useState(false);
  
  // Fetch quizzes from API
  useEffect(() => {
    const fetchQuizzes = async () => {
      setIsLoading(true);
      setError(null);
      
      try {
        // Build the query string from search params
        const params = new URLSearchParams(searchParams.toString());
        // Ensure page parameter is included
        params.set('page', page.toString());
        
        console.log("Fetching quizzes with params:", params.toString());
        const response = await fetch(`/api/users/me/quizzes?${params.toString()}`);
        const data = await response.json();
        
        if (!response.ok) {
          throw new Error(data.message || "Failed to fetch quizzes");
        }
        
        if (data.success) {
          console.log("API response:", data);
          setQuizzes(data.data.items);
          setTotalPages(data.data.totalPages);
          setTotalItems(data.data.totalItems);
          
          // Apply tab filtering
          filterQuizzesByTab(data.data.items, activeTab);
          
          // Show toast if no quizzes returned
          if (data.data.items.length === 0) {
            toast({
              title: "Cha c bi kim tra",
              description: "Hin ti cha c bi kim tra no trong lp hc ca bn.",
              variant: "default",
            });
          }
        } else {
          throw new Error(data.message || "Failed to fetch quizzes");
        }
      } catch (err) {
        console.error("Error fetching quizzes:", err);
        setError((err as Error).message);
        toast({
          title: "Li",
          description: (err as Error).message,
          variant: "destructive",
        });
      } finally {
        setIsLoading(false);
        setIsRefreshing(false);
      }
    };
    
    fetchQuizzes();
  }, [searchParams, page, toast]);
  
  // Apply tab filtering when active tab changes
  useEffect(() => {
    filterQuizzesByTab(quizzes, activeTab);
  }, [activeTab, quizzes]);
  
  // Filter quizzes by tab
  const filterQuizzesByTab = (quizzes: QuizType[], tab: string) => {
    if (tab === "all") {
      setFilteredQuizzes(quizzes);
    } else {
      setFilteredQuizzes(quizzes.filter(quiz => quiz.status === tab));
    }
  };
  
  // Handle tab change
  const handleTabChange = (value: string) => {
    setActiveTab(value);
  };
  
  // Handle page change
  const handlePageChange = (pageNumber: number) => {
    if (pageNumber === page || pageNumber < 1 || pageNumber > totalPages) {
      return;
    }
    setPage(pageNumber);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };
  
  // Handle refresh button click
  const handleRefresh = () => {
    setIsRefreshing(true);
    // Revalidate the current page
    const currentParams = new URLSearchParams(searchParams.toString());
    currentParams.set('page', page.toString());
    // Add a cache-busting parameter
    currentParams.set('_t', Date.now().toString());
    
    // Re-trigger the fetch by forcing a state change
    setPage(prev => {
      // This will force the useEffect to re-run even if the page number hasn't changed
      const temp = prev === 1 ? 2 : 1;
      setTimeout(() => setPage(prev), 0);
      return temp;
    });
  };
  
  // Generate pagination items
  const generatePaginationItems = () => {
    const items = [];
    const maxVisiblePages = 5;
    
    // Previous button
    items.push(
      <PaginationItem key="prev">
        <PaginationLink
          onClick={() => handlePageChange(page - 1)}
          className={page === 1 || isLoading ? "pointer-events-none opacity-50" : ""}
          aria-disabled={page === 1 || isLoading}
          aria-label="Go to previous page"
        >
          <span className="sr-only">Previous</span>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
            className="h-4 w-4"
          >
            <path d="M15 18l-6-6 6-6" />
          </svg>
        </PaginationLink>
      </PaginationItem>
    );
    
    // Always show the first page
    items.push(
      <PaginationItem key="page-1">
        <PaginationLink 
          isActive={page === 1} 
          onClick={() => handlePageChange(1)}
          aria-label="Page 1"
          aria-current={page === 1 ? "page" : undefined}
        >
          1
        </PaginationLink>
      </PaginationItem>
    );
    
    if (totalPages <= maxVisiblePages) {
      // If few pages, show all
      for (let i = 2; i <= totalPages; i++) {
        items.push(
          <PaginationItem key={`page-${i}`}>
            <PaginationLink 
              isActive={page === i}
              onClick={() => handlePageChange(i)}
              aria-label={`Go to page ${i}`}
              aria-current={page === i ? "page" : undefined}
            >
              {i}
            </PaginationLink>
          </PaginationItem>
        );
      }
    } else {
      // If many pages, show a subset with ellipsis
      // Display ellipsis if current page is far from the start
      if (page > 3) {
        items.push(
          <PaginationItem key="ellipsis-1">
            <PaginationEllipsis />
          </PaginationItem>
        );
      }
      
      // Display nearby pages
      const startPage = Math.max(2, page - 1);
      const endPage = Math.min(totalPages - 1, page + 1);
      
      for (let i = startPage; i <= endPage; i++) {
        items.push(
          <PaginationItem key={`page-${i}`}>
            <PaginationLink 
              isActive={page === i}
              onClick={() => handlePageChange(i)}
              aria-label={`Go to page ${i}`}
              aria-current={page === i ? "page" : undefined}
            >
              {i}
            </PaginationLink>
          </PaginationItem>
        );
      }
      
      // Display ellipsis if current page is far from the end
      if (page < totalPages - 2) {
        items.push(
          <PaginationItem key="ellipsis-2">
            <PaginationEllipsis />
          </PaginationItem>
        );
      }
      
      // Always show the last page
      if (totalPages > 1) {
        items.push(
          <PaginationItem key={`page-${totalPages}`}>
            <PaginationLink 
              isActive={page === totalPages}
              onClick={() => handlePageChange(totalPages)}
              aria-label={`Go to page ${totalPages}`}
              aria-current={page === totalPages ? "page" : undefined}
            >
              {totalPages}
            </PaginationLink>
          </PaginationItem>
        );
      }
    }
    
    // Next button
    items.push(
      <PaginationItem key="next">
        <PaginationLink
          onClick={() => handlePageChange(page + 1)}
          className={page === totalPages || isLoading ? "pointer-events-none opacity-50" : ""}
          aria-disabled={page === totalPages || isLoading}
          aria-label="Go to next page"
        >
          <span className="sr-only">Next</span>
          <svg
            xmlns="http://www.w3.org/2000/svg" 
            width="24" 
            height="24" 
            viewBox="0 0 24 24" 
            fill="none" 
            stroke="currentColor" 
            strokeWidth="2" 
            strokeLinecap="round" 
            strokeLinejoin="round"
            className="h-4 w-4"
          >
            <path d="M9 18l6-6-6-6" />
          </svg>
        </PaginationLink>
      </PaginationItem>
    );
    
    return items;
  };
  
  return (
    <div className="space-y-6 max-w-screen-2xl mx-auto">
      {/* Header with refresh button */}
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold sm:text-3xl">Bi kim tra</h1>
        <Button
          variant="outline"
          size="sm"
          onClick={handleRefresh}
          className="flex items-center gap-1.5"
          aria-label="Refresh list"
        >
          <RefreshCw className="h-3.5 w-3.5" />
          <span>Refresh</span>
        </Button>
      </div>
      
      {/* Quiz filters */}
      <QuizFilters />
      
      {/* Tabs for quick filtering */}
      <Tabs value={activeTab} onValueChange={handleTabChange}>
        <TabsList className="grid grid-cols-5 max-w-[750px]">
          <TabsTrigger value="all">Tt c</TabsTrigger>
          <TabsTrigger value="upcoming">Sp ti</TabsTrigger>
          <TabsTrigger value="ongoing">C th lm</TabsTrigger>
          <TabsTrigger value="completed"> hon thnh</TabsTrigger>
          <TabsTrigger value="expired"> ht hn</TabsTrigger>
        </TabsList>
        
        <TabsContent value={activeTab} className="mt-6">
          {isLoading ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {Array.from({ length: 6 }).map((_, i) => (
                <QuizCardSkeleton key={i} />
              ))}
            </div>
          ) : error ? (
            <Alert variant="destructive">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>Li</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          ) : filteredQuizzes.length === 0 ? (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12 text-center">
                <div className="rounded-full bg-muted p-3 text-foreground mb-4">
                  <BookOpen className="h-6 w-6" />
                </div>
                <h3 className="text-lg font-medium">Khng tm thy bi kim tra</h3>
                <p className="text-muted-foreground mt-1 max-w-md">
                  {activeTab === "all" 
                    ? "Khng c bi kim tra no ph hp vi b lc hin ti." 
                    : `Khng c bi kim tra no  trng thi "${
                        activeTab === "upcoming" ? "sp ti" : 
                        activeTab === "ongoing" ? "sn sng" : 
                        " hon thnh"
                      }".`}
                </p>
              </CardContent>
              <CardFooter className="justify-center">
                <Button 
                  variant="outline" 
                  onClick={() => handleTabChange("all")} 
                  className="mt-2"
                >
                  Xem tt c bi kim tra
                </Button>
              </CardFooter>
            </Card>
          ) : (
            <div className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {filteredQuizzes.map((quiz) => (
                  <QuizCard key={quiz.id} quiz={quiz} />
                ))}
              </div>
              
              {/* Show pagination if there are multiple pages */}
              {totalPages > 1 && (
                <div className="flex flex-col items-center gap-2">
                  <Pagination>
                    <PaginationContent>
                      {generatePaginationItems()}
                    </PaginationContent>
                  </Pagination>
                  
                  <p className="text-sm text-muted-foreground">
                    Hin th {filteredQuizzes.length} trong tng s {totalItems} bi kim tra
                  </p>
                </div>
              )}
            </div>
          )}
        </TabsContent>
      </Tabs>
    </div>
  );
}